import { BACKGROUNDCOLOR } from "../Helpers/colors";

const PriceTable = () =>{
    return(
        <>
            <div className="p-4 " style={{backgroundColor:BACKGROUNDCOLOR}}>
                <div className="container">
                    <h3 className="text-center">Current Prices</h3>
                <table class="table table-bordered mt-4" style={{direction:"rtl"}}>
                  <thead>
                    <tr>
                      <th scope="col">#</th>
                      <th scope="col">First</th>
                      <th scope="col">Last</th>
                      <th scope="col">Handle</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <th scope="row">1</th>
                          <td>Mark</td>
                          <td>Otto</td>
                          <td>@mdo</td>
                    </tr>
                    <tr>
                      <th scope="row">2</th>
                      <td>Jacob</td>
                      <td>Thornton</td>
                      <td>@fat</td>
                    </tr>
                    <tr>
                      <th scope="row">3</th>
                      <td colspan="2">Larry the Bird</td>
                      <td>@twitter</td>
                    </tr>
                  </tbody>
                </table>
                </div>
            </div>
        </>
    )
}

export default PriceTable;